import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../globalComponents'
import { createShortName } from '../../../utils/common'
import { CustCheckbox, Input, Label } from '../../commanComponent'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addUpdateTrainingCertificateSchema } from '../../../utils/ValidationSchema'
import { useSelector } from 'react-redux'
import { useCustomTranslation } from '../../../translation'

const CreateUpdatetrainingCertificate = (props) => {
    const { t } = useCustomTranslation()
    const { addTrainingCertificatePending, updateTrainingCertificatePending } = useSelector(state => state?.trainingCertificate)
    const {
        selectedTrainingCertificateData,
        openModel,
        handleOpenClose,
        handleCreateUpdateTrainingCertificate,
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateTrainingCertificatesFormData, setCreateUpdateTrainingCertificatesFormData] = useState({
        name: '',
        short_name: '',
        is_active: true
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreateUpdateTrainingCertificatesFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === 'name') {
                updatedState.short_name = createShortName(value);
            }
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateTrainingCertificate = async () => {
        const validationResponse = await validateSchema(addUpdateTrainingCertificateSchema, createUpdateTrainingCertificatesFormData);
        if (validationResponse.isValid) {
            const {
                name,
                short_name,
                is_active,
            } = createUpdateTrainingCertificatesFormData
            const params = {
                name,
                short_name,
                is_active,

            }
            setErrors({})
            handleCreateUpdateTrainingCertificate(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedTrainingCertificateData) {
            setCreateUpdateTrainingCertificatesFormData({
                name: selectedTrainingCertificateData?.name,
                short_name: selectedTrainingCertificateData?.short_name,
                is_active: selectedTrainingCertificateData?.is_active
            })
        }
    }, [selectedTrainingCertificateData])

    const resetForm = () => {
        setCreateUpdateTrainingCertificatesFormData({
            is_active: true,
            name: '',
            short_name: '',
        })
        setErrors({})
    }
    return (
        <>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "800px"
            }}
            id="interviewDetail"
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedTrainingCertificateData ? "Update" : "Add"} Training Certificates</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => handleOpenClose()}
                />
            </div>

            <div className="offcanvas-body myRequest ">
                <div className="formPanel">
                    <div className="row">

                        <div className="col-md-3">
                            <div className="form-group mb-3">
                                <Label text={t("labels.trainingCertificatesName")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.trainingCertificatesName")}
                                    value={createUpdateTrainingCertificatesFormData?.name}
                                    name="name"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.name && (
                                    <CustomError text={errors.name} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-3">
                            <div className="form-group mb-3">
                                <Label text={t("labels.shortName")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <input type="text" className="form-control fs-7"
                                    name='short_name'
                                    placeholder={t("placeholders.shortName")}
                                    value={createUpdateTrainingCertificatesFormData?.short_name}
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.short_name && (
                                    <CustomError text={errors.short_name} />
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-3">
                            <div className="form-group mb-3">
                                <Label text={t("labels.isActive")} required={true} />
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <CustCheckbox
                                    key={"is_active"}
                                    id={`is_active`}
                                    // label={"Active"}
                                    checked={createUpdateTrainingCertificatesFormData?.is_active}
                                    handleChange={(e) => handleFromChange(e)}
                                    name={"is_active"}
                                />

                            </div>
                        </div>
                    </div>


                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={selectedTrainingCertificateData ? t("btns.update") : t("btns.submit")}
                        classNames={"btn btn-org-gradient"}
                        handleClick={() => submitCreateUpdateTrainingCertificate()}
                        isLoading={selectedTrainingCertificateData ? updateTrainingCertificatePending : addTrainingCertificatePending}
                    />
                </div>
            </div>
        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateUpdatetrainingCertificate
