import React, { useEffect, useState } from 'react'
import { CustomButton } from '../../globalComponents'
import { useSelector, useDispatch } from 'react-redux'
import { trainingCertificatesActions } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
import TrainingCertificatesTable from './TrainingCertificatesTable'
import CreateUpdatetrainingCertificate from './CreateUpdatetrainingCertificate'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'

const TrainingCertificate = () => {
    const {t}=useCustomTranslation()
    const dispatch = useDispatch()
    const {
        addTrainingCertificatePending,
        addTrainingCertificateRes,
        addTrainingCertificateError,

        updateTrainingCertificatePending,
        updateTrainingCertificateRes,
        updateTrainingCertificateError,

        deleteTrainingCertificatePending,
        deleteTrainingCertificateRes,
        deleteTrainingCertificateError,

        getAllTrainingCertificatesPending,
        getAllTrainingCertificatesRes,
        getAllTrainingCertificatesError,

    } = useSelector(state => state.trainingCertificate)

    const [isOpenCreateUpdateTrainingCertificate, setIsOpenCreateUpdateTrainingCertificate] = useState(false)
    const [selectedTrainingCertificateData, setSelectedTrainingCertificateData] = useState('')
    const [currentPage, setCurrentPage] = useState(1)
    const [isShowDeleteTrainingCertificateModal, setIsShowDeleteTrainingCertificateModal] = useState(false)
    const [deleteTrainingCertificateId, setDeleteTrainingCertificateId] = useState('')
    const [allTrainingCertificates, setAllTrainingCertificates] = useState('')

    // Create Update TrainingCertificate.... 
    const handleCreateUpdateTrainingCertificateModal = (data) => {
        if(!data){
            setSelectedTrainingCertificateData('')
        }else{
            setSelectedTrainingCertificateData(data)
        }
        setIsOpenCreateUpdateTrainingCertificate(!isOpenCreateUpdateTrainingCertificate)
    }

    const handleCreateUpdateTrainingCertificate = (params) => {
        if (selectedTrainingCertificateData) {
            params['_id'] = selectedTrainingCertificateData?.id
            dispatch(trainingCertificatesActions.updateTrainingCertificateRequest(params));
        }
        else {
            dispatch(trainingCertificatesActions.addTrainingCertificateRequest(params));
        }
    }

    useEffect(() => {
        if (!addTrainingCertificatePending) {
            if (addTrainingCertificateError) {
                dispatch(trainingCertificatesActions.handleClearTrainingCertificateStore("HANDLE_CLEAR_ADD_TRAINING_CERTIFICATE_STORE"))
                return;
            }
            if (addTrainingCertificateRes) {
                if (addTrainingCertificateRes?.code === 200) {
                    dispatch(trainingCertificatesActions.handleClearTrainingCertificateStore("HANDLE_CLEAR_ADD_TRAINING_CERTIFICATE_STORE"))
                    getAllTrainingCertificates()
                    setIsOpenCreateUpdateTrainingCertificate(!isOpenCreateUpdateTrainingCertificate)
                }
            }
        }
    }, [
        addTrainingCertificateRes,
        addTrainingCertificateError,
        addTrainingCertificatePending
    ]);


    useEffect(() => {
        if (!updateTrainingCertificatePending) {
            if (updateTrainingCertificateError) {
                dispatch(trainingCertificatesActions.handleClearTrainingCertificateStore("HANDLE_CLEAR_UPDATE_TRAINING_CERTIFICATE_STORE"))
                return;
            }
            if (updateTrainingCertificateRes) {
                if (updateTrainingCertificateRes?.code === 200) {
                    dispatch(trainingCertificatesActions.handleClearTrainingCertificateStore("HANDLE_CLEAR_UPDATE_TRAINING_CERTIFICATE_STORE"))
                    getAllTrainingCertificates()
                    setIsOpenCreateUpdateTrainingCertificate(!isOpenCreateUpdateTrainingCertificate)
                }
            }
        }
    }, [
        updateTrainingCertificateRes,
        updateTrainingCertificateError,
        updateTrainingCertificatePending
    ]);

    //GET ALL TrainingCertificates DATA
    useEffect(() => {
        getAllTrainingCertificates()
    }, [currentPage])

    const getAllTrainingCertificates = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(trainingCertificatesActions.getAllTrainingCertificatesRequest(params));
    }

    useEffect(() => {
        if (!getAllTrainingCertificatesPending) {
            if (getAllTrainingCertificatesError) {
                return;
            }
            if (getAllTrainingCertificatesRes) {
                if (getAllTrainingCertificatesRes?.code === 200) {
                    setAllTrainingCertificates(getAllTrainingCertificatesRes?.data)
                }
            }
        }
    }, [
        getAllTrainingCertificatesRes,
        getAllTrainingCertificatesError,
        getAllTrainingCertificatesPending
    ]);



    // DELETE TrainingCertificate ............ 
    const handleTrainingCertificateDeleteModal = (id) => {
        setDeleteTrainingCertificateId(id)
        setIsShowDeleteTrainingCertificateModal(!isShowDeleteTrainingCertificateModal)
    }

    const handleDeleteTrainingCertificate = (type) => {
        if (type) {
            const params = {
                _id: deleteTrainingCertificateId
            }
            dispatch(trainingCertificatesActions?.deleteTrainingCertificateRequest(params))

        } else {
            setIsShowDeleteTrainingCertificateModal(!isShowDeleteTrainingCertificateModal)
        }

    }

    useEffect(() => {
        if (!deleteTrainingCertificatePending) {
            if (deleteTrainingCertificateError) {
                dispatch(trainingCertificatesActions.handleClearTrainingCertificateStore("HANDLE_CLEAR_DELETE_TRAINING_CERTIFICATE_STORE"))
                return;
            }
            if (deleteTrainingCertificateRes) {
                if (deleteTrainingCertificateRes?.code === 200) {
                    setIsShowDeleteTrainingCertificateModal(!isShowDeleteTrainingCertificateModal)
                    if(currentPage >1 && allTrainingCertificates?.data?.length===1){
                        setCurrentPage((pre=> pre -= 1 ))
                    }else{
                        getAllTrainingCertificates()
                    }
                    dispatch(trainingCertificatesActions.handleClearTrainingCertificateStore("HANDLE_CLEAR_DELETE_TRAINING_CERTIFICATE_STORE"))
                }
            }
        }
    }, [
        deleteTrainingCertificateRes,
        deleteTrainingCertificateError,
        deleteTrainingCertificatePending
    ]);
    return (
        <>
            <div className="d-flex  justify-content-end ">
                <CustomButton
                    classNames={"btn btn-primary gradientBg"}
                    handleClick={() => handleCreateUpdateTrainingCertificateModal()}
                    text={t("btns.createTrainingCertificate")}
                    slug={"create_training_certificates"}
                />
            </div>
            <CreateUpdatetrainingCertificate
                openModel={isOpenCreateUpdateTrainingCertificate}
                handleOpenClose={handleCreateUpdateTrainingCertificateModal}
                handleCreateUpdateTrainingCertificate={handleCreateUpdateTrainingCertificate}
                selectedTrainingCertificateData={selectedTrainingCertificateData}
            />
            <TrainingCertificatesTable
                setSelectedTrainingCertificateData={setSelectedTrainingCertificateData}
                handleCreateUpdateTrainingCertificateModal={handleCreateUpdateTrainingCertificateModal}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                handleTrainingCertificateDeleteModal={handleTrainingCertificateDeleteModal}
                allTrainingCertificates={allTrainingCertificates}
            />

            {isShowDeleteTrainingCertificateModal && (
                <ApproveRejectModal
                    onApproveReject={handleDeleteTrainingCertificate}
                    isShow={isShowDeleteTrainingCertificateModal}
                    desc={t("alerts.deletePopup.desc").replace('$1',"Training Certificate")}
                    btnRightLoading={deleteTrainingCertificatePending}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}

                />)
            }
        </>
    )
}

export default TrainingCertificate
