import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor, isPermission } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../translation'
import { Icon } from '../../commanComponent'

const TrainingCertificatesTable = (props) => {
    const { t } = useCustomTranslation()
    const {
        isLoading,
        setSelectedTrainingCertificateData,
        handleCreateUpdateTrainingCertificateModal,
        handleTrainingCertificateDeleteModal,
        currentPage,
        setCurrentPage,
        allTrainingCertificates
    } = props
    const [trainingCertificateList, setTrainingCertificateList] = useState('')
    const updateTrainingCertPer = isPermission("update_training_certificates")
    const deleteTrainingCertPer = isPermission("delete_training_certificates")


    const handleUpdateTrainingCertificate = (data) => {
        handleCreateUpdateTrainingCertificateModal(data)
    }

    useEffect(() => {
        setTrainingCertificateList(allTrainingCertificates)
    }, [allTrainingCertificates])


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                   {updateTrainingCertPer && <li>
                        <span className="dropdown-item"
                            onClick={() => handleUpdateTrainingCertificate(item)}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>}
                   {deleteTrainingCertPer && <li>
                        <span className="dropdown-item"
                            onClick={() => handleTrainingCertificateDeleteModal(item.id)}
                        >
                            <Icon className={"bi bi-trash me-1"} />Delete</span>
                    </li>}
                </ul>
            </div>

        )
    }), [setSelectedTrainingCertificateData, handleCreateUpdateTrainingCertificateModal]);

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'name', label: 'Training Certificates Name' },
        { key: 'short_name', label: 'Short Name' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
       ((updateTrainingCertPer ||deleteTrainingCertPer)  &&{ key: 'action', label: 'Action' })
    ];
    return (
        <>
            <CustomTable
                data={trainingCertificateList?.data}
                fields={fields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={trainingCertificateList?.totalPages}
                tableName={t("headings.trainingCertificates")}
                // onSearch={handleSearch}
                scopedSlots={scopedSlots}
            />
        </>
    )
}

export default TrainingCertificatesTable
